.input{
    height: 75px;
    border-radius: 35px;
    margin-bottom: 20px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    font-weight: bold;
    font-size: 30px;
    
    background-color: rgba(206, 212, 203, 0.871);
    color: black;
    padding: 11px 30px 11px 11px;
    margin: 1px solid #888;
    box-shadow: -1px 2px 1px 1px rgba(232, 232, 232, 0.936);
}